const FlexbaseOrange = '#FF5745';
const FlexbaseOrange2 = '#FF8B7E';
const PayWithFlexbaseStyle = `
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

#flexbase-pay-btn {
    width: 366px;
    height: 52.18px;
    background: linear-gradient(125deg, ${FlexbaseOrange} 40%, ${FlexbaseOrange2} 100%);
    display: flex;
    color: white;
    align-items: center;
    justify-content: space-between;
    font-weight: 700;
    border-radius: 8px;
    border-color: transparent;
    padding-left: 20px;
    padding-right: 20px;
    font-family: sans-serif;
    text-decoration: none;
}

#flexbase-pay-btn-text {
    font-size: 20px;
}

#flexbase-pay-btn-logo {
    font-family: 'Roboto', sans-serif;
    font-size: 26px;
}

@media screen and (max-width: 480px) {
    #flexbase-pay-btn {
        width: 252px;
    }
`;

/* eslint-disable  @typescript-eslint/ban-types */
// eslint-disable-next-line  @typescript-eslint/no-explicit-any
const createFlexbaseElement = (tagName, props, ...children) => {
    props = props || {};
    if (typeof tagName === 'function') {
        return tagName(props, children);
    }
    const element = document.createElement(tagName);
    Object.entries(props).forEach(([name, value]) => {
        if (name.startsWith('on') && name.toLowerCase() in window) {
            element.addEventListener(name.toLowerCase().substring(2), value);
        }
        else {
            element.setAttribute(name, value);
        }
    });
    children.forEach(child => appendChild(element, child));
    return element;
};
const appendChild = (parent, child) => {
    if (Array.isArray(child)) {
        child.forEach(nestedChild => appendChild(parent, nestedChild));
    }
    else if (child instanceof Node) {
        parent.appendChild(child);
    }
    else {
        parent.appendChild(document.createTextNode(child));
    }
};

const PayWithFlexbaseTemplate = () => {
    return (createFlexbaseElement("div", { id: "flexbase-pay" },
        createFlexbaseElement("a", { href: "https://flexbase.app", id: "flexbase-pay-btn" },
            createFlexbaseElement("div", { id: "flexbase-pay-btn-text" }, "Pay in 60 days with"),
            createFlexbaseElement("div", { id: "flexbase-pay-btn-logo" }, " flexbase"))));
};

class PayWithFlexbase extends HTMLElement {
    static get observedAttributes() {
        return ['apikey', 'amount', 'callback', 'session', 'flexbaseDomain'];
    }
    constructor() {
        super();
        const shadowRoot = this.attachShadow({ mode: 'open' });
        const style = document.createElement('style');
        style.textContent = PayWithFlexbaseStyle;
        shadowRoot.appendChild(style);
        const template = PayWithFlexbaseTemplate();
        shadowRoot.appendChild(template);
        const anchor = shadowRoot.querySelector('a');
        if (anchor) {
            anchor.href = this.buildUrl();
        }
    }
    updateHref() {
        const shadowRoot = this.shadowRoot;
        if (!shadowRoot) {
            return;
        }
        const anchor = shadowRoot.querySelector('a');
        if (anchor) {
            anchor.href = this.buildUrl();
        }
    }
    get apikey() {
        return this.getAttribute('apikey');
    }
    set apikey(value) {
        value ? this.setAttribute('apikey', value) : this.removeAttribute('apikey');
    }
    get amount() {
        return Number(this.getAttribute('amount'));
    }
    set amount(value) {
        value ? this.setAttribute('amount', value.toString()) : this.removeAttribute('amount');
    }
    get callback() {
        return this.getAttribute('callback');
    }
    set callback(value) {
        value ? this.setAttribute('callback', value) : this.removeAttribute('callback');
    }
    get session() {
        return this.getAttribute('session');
    }
    set session(value) {
        value ? this.setAttribute('session', value) : this.removeAttribute('session');
    }
    get flexbaseDomain() {
        return this.getAttribute('flexbaseDomain');
    }
    set flexbaseDomain(value) {
        value ? this.setAttribute('flexbaseDomain', value) : this.removeAttribute('flexbaseDomain');
    }
    // eslint-disable-next-line  @typescript-eslint/no-explicit-any
    attributeChangedCallback(_name, _oldValue, _newValue) {
        this.updateHref();
    }
    buildUrl() {
        const apikey = this.apikey || '';
        const amount = this.amount || 0;
        const callback = this.callback || '';
        const session = this.session || '';
        const q = this.buildQuery({
            apikey,
            amount,
            callback,
            session,
        });
        const url = new URL('credit/pay', this.flexbaseDomain || 'https://dashboard.flexbase.app');
        url.search = q;
        return url.toString();
    }
    // eslint-disable-next-line  @typescript-eslint/no-explicit-any
    buildQuery(params) {
        return Object.keys(params)
            .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(params[key]))
            .join('&');
    }
}

customElements.define('pay-with-flexbase', PayWithFlexbase);

export { PayWithFlexbase, PayWithFlexbaseStyle };
